import React from "react";
import { useTranslation } from "react-i18next";
import Flip from "react-reveal/Flip";

function Showcase() {
  const { t } = useTranslation();
  return (
    <div className="img-wrapper">
      <Flip>
        <h1>{t("Elevated Thinking")}</h1>
      </Flip>
    </div>
  );
}

export default Showcase;
