import React from "react";
import { useTranslation } from "react-i18next";
import Roll from "react-reveal/Roll";
import Fade from "react-reveal/Fade";

const TextPage = () => {
  const { t } = useTranslation();
  return (
    <div className="TextPage">
      <div className="wrapper">
        <Roll top>
          <h1 className="title">
            {t(
              "You want your elevator to be as safe, efficient and appealing as possible"
            )}
          </h1>
        </Roll>
        <br />
        <br />
        <Fade right>
          <p>
            {t(
              "Modernization brings you these key benefits when your system is starting to lose its shine and performance  for significantly less than a new elevator."
            )}
          </p>
        </Fade>
      </div>
    </div>
  );
};

export default TextPage;
