import React from "react";
import { useTranslation } from "react-i18next";
import { Accordion } from "react-bootstrap";
import "./House.style.scss";
import Fade from "react-reveal/Fade";

const HouseAccordion = () => {
  const { t } = useTranslation();

  return (
    <Accordion className="accordion" defaultActiveKey="0">
      <Fade right>
        <Accordion.Item eventKey="0">
          <Accordion.Header id="accordion-header">
            <h4> {t(" Escalators & Moving Walkways / Model T ")}</h4>
          </Accordion.Header>
          <Accordion.Body>
            <h4>{t("Model T")}</h4>
            <br />
            <b>
              {t("Our flagship heavy-duty escalator suitable for outdoor use")}
            </b>
            <br />
            <p>
              {t(
                " This super, heavy-duty escalator is the workhorse in our line up, designed for moving heavy passenger loads in subways, airports and train stations, safely and comfortably with ease."
              )}
            </p>
            <br />
          </Accordion.Body>
        </Accordion.Item>
      </Fade>

      <Fade left>
        <Accordion.Item eventKey="1">
          <Accordion.Header id="accordion-header">
            <h4> {t(" Escalators & Moving Walkways / Model T ")}</h4>
          </Accordion.Header>
          <Accordion.Body>
            <h4>{t("Model T")}</h4>
            <br />
            <b>
              {t("Our flagship heavy-duty escalator suitable for outdoor use")}
            </b>
            <br />
            <p>
              {t(
                " This super, heavy-duty escalator is the workhorse in our line up, designed for moving heavy passenger loads in subways, airports and train stations, safely and comfortably with ease."
              )}
            </p>
            <br />
          </Accordion.Body>
        </Accordion.Item>
      </Fade>

      <Fade right>
        <Accordion.Item eventKey="2">
          <Accordion.Header id="accordion-header">
            <h4> {t(" Escalators & Moving Walkways / Model T ")}</h4>
          </Accordion.Header>
          <Accordion.Body>
            <h4>{t("Model T")}</h4>
            <br />
            <b>
              {t("Our flagship heavy-duty escalator suitable for outdoor use")}
            </b>
            <br />
            <p>
              {t(
                " This super, heavy-duty escalator is the workhorse in our line up, designed for moving heavy passenger loads in subways, airports and train stations, safely and comfortably with ease."
              )}
            </p>
            <br />
          </Accordion.Body>
        </Accordion.Item>
      </Fade>
      <Fade left>
        <Accordion.Item eventKey="3">
          <Accordion.Header id="accordion-header">
            <h4> {t(" Escalators & Moving Walkways / Model T ")}</h4>
          </Accordion.Header>
          <Accordion.Body>
            <h4>{t("Model T")}</h4>
            <br />
            <b>
              {t("Our flagship heavy-duty escalator suitable for outdoor use")}
            </b>
            <br />
            <p>
              {t(
                " This super, heavy-duty escalator is the workhorse in our line up, designed for moving heavy passenger loads in subways, airports and train stations, safely and comfortably with ease."
              )}
            </p>
            <br />
          </Accordion.Body>
        </Accordion.Item>
      </Fade>
    </Accordion>
  );
};

export default HouseAccordion;
