import React from "react";
import { useTranslation } from "react-i18next";
import "./news.style.scss";
function BetaNews() {
  const { t } = useTranslation();
  return (
    <section className="news-section-2  justify-content-center">
      <div className="news-section-2-text">
        <h1 className="news-section-2-h1">{t("BETA LEFT ASIA NEWS")}</h1>
        <div className="news-section-2-line"></div>
        <p className="news-section-2-p">Check out our latest news releases</p>
      </div>
    </section>
  );
}

export default BetaNews;
