import React, { useState, useEffect } from "react";
import { useLocation } from "react-router";
import News_Intro from "./components/News_Intro";
// import NewsPosts from "./components/NewsPosts/NewsPosts";
import NewsSection_3 from "./components/NewsSection_3";
import ForMedia from "./components/ForMedia";
// import Single from "./components/Single/Single";
import News_Carousel from "./components/News-Carousel";
import News_LastPage_Img from "./components/News_LastPage_Img";
import Posts from "../../Pages/Dashboard/components/Posts/Posts";
import BetaNews from "./components/BetaNews";
import axios from "axios";
function News() {
  const [posts, setPosts] = useState([]);
  const { search } = useLocation();

  useEffect(() => {
    const fetchPosts = async () => {
      const res = await axios.get("/posts" + search);
      setPosts(res.data);
    };
    fetchPosts();
  }, [search]);

  return (
    <div>
      <News_Intro />
      <BetaNews />
      <Posts posts={posts} />
      <NewsSection_3 />
      <News_Carousel />
      <ForMedia />
      <News_LastPage_Img />
    </div>
  );
}

export default News;
