// Import React
import React from "react";

// Reactstrap Components
import { Container, Row, Col } from "reactstrap";

// Import Local Component
import ProductItem from "../ProductItem";

// Import Backogrund Image
import BackgroundImage from "../productsBackground.jpg";

// Import Product Items Images
import productItem1 from "../productItem1.jpg";
import productItem2 from "../productItem2.jpg";
import productItem3 from "../productItem3.jpg";
import ProductsShowcase from "./ProductsShowcase";

// Styles
import "./Products.style.scss";

// Import Translation Module
import { useTranslation } from "react-i18next";

import Flip from "react-reveal/Flip";
import Fade from "react-reveal/Fade";
import Roll from "react-reveal/Roll";
// Products Component
function Products() {
  const { t } = useTranslation();

  return (
    <section id="products" className="products">
      {/* <header>
        <img src={BackgroundImage} alt="Products Background" />
        <Flip>
          <h1 className="text-center">{t("Products")}</h1>
        </Flip>
      </header> */}
      <ProductsShowcase />
      <main>
        <article className="main-part">
          <Container>
            <Row className="d-flex flex-column align-items-center">
              <Col className="main-info col-12 col-lg-7 text-center">
                <Roll top>
                  <h2>{t("Leading the way on high rise elevators")}</h2>
                </Roll>
                <Fade right>
                  <p>
                    {t(
                      "Otis offers the best solutions to manage vertical transportation in elevators in high rise buildings. High rise buildings are classified as buildings with more than 12 floors, and can require custom designs for managing car arrangements with single, double, and super double deck solutions as well as lobby arrangements. In addition, elevator planning for high rise buildings with Otis includes performance equipment design for high speed, as well as counting in factors like performance, quality and high traffic."
                    )}
                  </p>
                </Fade>
                <Fade left>
                  <p>
                    {t(
                      "Reach out to Otis to start building your custom solution today."
                    )}
                  </p>
                </Fade>
                <Fade right>
                  <a href="#!">{t("contact us")}</a>
                </Fade>
              </Col>
              <Col className="main-products col-12 text-center">
                <Roll top>
                  <h4>{t("elevator products")}</h4>
                </Roll>
                <Row className="d-flex flex-row justify-content-start flex-wrap">
                  <ProductItem image={productItem1} title="Gen2" />
                  <ProductItem image={productItem2} title="Skyrise" />
                  <ProductItem image={productItem3} title="Skybuild" />
                </Row>
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </section>
  );
}

export default Products;
