import React from "react";
import { useTranslation } from "react-i18next";
import Roll from "react-reveal/Roll";
import Fade from "react-reveal/Fade";

const LookFor = () => {
  const { t } = useTranslation();
  return (
    <div className="CareersTextPage">
      <div className="wrapper">
        <Roll top>
          <h1 className="title">
            {t("We are looking for people who grow thanks to new challenges")}
          </h1>
        </Roll>
        <br />
        <br />
        <Fade right>
          <p>
            {t(
              "At Beta Lift Asia, we know that greatness is built every day and that the passion, dedication and hard work of our employees make our continued success possible. We lead our industry because our employees today do more than just meet the needs of our customers. They anticipate the needs of tomorrow. We do our best to hire and develop the best employees, and we offer a supportive environment in which employees are motivated and have the opportunity to reach their full potential. Today, we continue to expand the horizons of what can succeed in a higher, faster, and smarter world."
            )}
          </p>
        </Fade>
        <br />
        <br />
        <Fade left>
          <button className="button-dark">
            SCHOLARSHIP PROGRAM FOR EMPLOYEES
          </button>
        </Fade>
      </div>
    </div>
  );
};

export default LookFor;
