import React from "react";
import { useTranslation } from "react-i18next";
import Roll from "react-reveal/Roll";
import Fade from "react-reveal/Fade";

function Text() {
  const { t } = useTranslation();
  return (
    <div className="container">
      <div className="text-wrapper">
        <Roll top>
          <h1 className="title">
            {t("Taking ideas and innovation to all-new levels")}
          </h1>
        </Roll>

        <Fade left>
          <p>
            {t(
              "At Beta Lift Asia, we are Made to Move You. And that means more than enabling passenger journeys. Its about pushing the boundaries of whats possible for an entire industry, taking ideas and innovation to all-new levels and reinventing entire experiences for passengers and customers."
            )}
          </p>
        </Fade>

        <br />
        <br />
        <Fade right>
          <p>
            {t(
              "Every day, Otis is bringing passion and expertise to address new challenges and uncover new possibilities. Were partnering with leading researchers to provide science-based answers and guidance when theyre needed most. Were empowering customers to take advantage of artificial intelligence and the internet of things (IoT). And were inspiring a new generation of engineers and leaders that will drive the industry forward."
            )}
          </p>
        </Fade>
        <br />
        <br />
        <Fade left>
          <p>
            {t(
              "After all, when you create and lead an industry thats all about movement, standing still is never an option. Check out the latest in innovation and thought leadership at Otis, and see where we can go together."
            )}
          </p>
        </Fade>
      </div>
    </div>
  );
}

export default Text;
