import React from "react";
import "./sections.style.scss";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import Roll from "react-reveal/Roll";
import Fade from "react-reveal/Fade";

function Section_1() {
  const { t } = useTranslation();
  return (
    <div className="aboutus-container">
      <section className="aboutUsSection-1">
        <img
          className="d-block w-100 eskalator"
          src="https://omuscmslfrpcdn03.azureedge.net/documents/256045/3900859/Otis_escalators_ourcompany_header2.jpg/cdd2b228-2b2d-06b1-43ab-854c5ebc6688?t=1606877463540"
        />
        <h1 className="aboutus-section-1-text">{t("About Us")}</h1>
        <div className="aboutus-section-1-stick"></div>
      </section>
      <section className="aboutus-section-2">
        <div className="aboutus-section-2-div"></div>
        <Roll top>
          <h1 className="aboutus-section-2-h1">{t("Made to move you")}</h1>
        </Roll>
        <div className="aboutus-section-2-stick"></div>
        <Fade right>
          <p className="aboutus-section-2-p">
            {t(
              "We move 2 billion people a day and maintain more than 2 million customer units worldwide, the industry's largest portfolio. We can be found in many of the worlds most recognizable buildings as well as the busiest transportation hubs and retail centers. We are everywhere   people are on the move."
            )}
          </p>
        </Fade>
        <Fade left>
          <div className="aboutus-section-2-button">
            <Link to="#" className="btn btn-1">
              {t("ANNUAL REPORT")}
            </Link>
            <Link to="#" className="btn btn-2">
              {t("FACTSHEET")}
            </Link>
          </div>
        </Fade>
      </section>
    </div>
  );
}

export default Section_1;
