import React from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { FaAngleDoubleRight } from "react-icons/fa";
import "./index.scss";
import Fade from "react-reveal/Fade";
import Roll from "react-reveal/Roll";

const ShoppingMall = () => {
  const { t } = useTranslation();
  return (
    <>
      <div className="lift ">
        <div className="lift__contact">
          <div className="lift-item">
            <h2 className="text-white text-uppercase">
              {t("Shopping Mall, Business Center")}
            </h2>
          </div>
        </div>

        <div className="content1 mt-3">
          <ul>
            <li>
              <Link className="link" to="https://ake.com.tr/">
                {t("Homepage")}
              </Link>
            </li>
            <FaAngleDoubleRight />
            <li>
              <Link
                className="link"
                to="https://ake.com.tr/products-usage-areas"
              >
                {t("Products &amp; Usage Areas")}
              </Link>
            </li>
            <FaAngleDoubleRight />
            <li>
              <Link className="link" to="https://ake.com.tr/usage-areas">
                {t("Usage areas")}
              </Link>
            </li>
            <FaAngleDoubleRight />
            <li>{t("Shopping Mall, Business Center")}</li>
          </ul>
        </div>
      </div>
      <div className="desk">
        <div className=" section">
          <div className="container fullge">
            <div className="Section-full">
              <Roll top>
                <h4 style={{ display: "inline-block" }}>
                  {t("Mall, Business & Event Center")}
                </h4>
              </Roll>
              <div className="section-title">
                <Fade left>
                  <p>
                    {t(
                      "We offer quick and safe alternatives to ensure the of heavy pedestrian traffic. You can analyze the options for suitable elevator, escalator and disability  systems  for yourhah business and event centers. We provide a lifetime service for all AKE products, from production to assembly and service maintenance"
                    )}
                  </p>
                </Fade>
              </div>
              <div className="section-tim">
                <Fade right>
                  <h2>
                    {t("Our Products for Shopping Mall, Business Center")}
                  </h2>
                </Fade>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="fotter">
        <div className="container1">
          <div className="row   justify-content: space-between">
            <div className="col-sm-6 col-md-4 row1">
              <Link>
                <img
                  src="https://ake.com.tr/uploads/images/logo.png"
                  alt="logo"
                />
              </Link>
            </div>
            <div className="col-sm-6 col-md-4">
              <span>
                {t(
                  " 2021 AKE Asansr, Yryen Merdiven ve Engelli  Sistemleri. All Rights Reserved."
                )}
              </span>
            </div>
            <div className="col-sm-6 col-md-4">
              <Link className="cool" to="https://ake.com.tr/home">
                Home
              </Link>
              <Link className="foot" to="https://ake.com.tr/home">
                /
              </Link>
              <Link className="foot" to="https://ake.com.tr/home">
                /
              </Link>
              <Link className="foot" to="https://ake.com.tr/home">
                /
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ShoppingMall;
