import React from "react";
import Carousel from "react-bootstrap/Carousel";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import firstImg from "./images/slide-img-1.jpg";
import secondImg from "./images/slide-img-2.jpg";
import fourthImg from "./images/slide-img-4.jpg";

function CarouselComponent() {
  const { t } = useTranslation();
  return (
    <div className="carousel-wrapper">
      <div className="Carousel">
        <Carousel fade>
          <Carousel.Item>
            <img className="d-block w-100" src={firstImg} alt="First slide" />
            <div className="carousel-item-text-wrapper">
              <h2 className="carousel-title ">{t("The new Gen3 elevator")}</h2>
              <p>{t("Meet our fully connected platform.")}</p>
              <Link to="#" className="carousel-button">
                {t("Learn More")}
              </Link>
            </div>
          </Carousel.Item>

          <Carousel.Item>
            <img className="d-block w-100" src={secondImg} alt="Second slide" />
            <div className="carousel-item-text-wrapper">
              <h2>
                {t('"Connected Elevators')} <br className="break" />
                {t("is our collective future.")} <br className="break" />
                {t('Welcome to Tomorrow"')}
              </h2>
              <p>{t("-Judy Marks, Beta Lift Asia president & CEO")}</p>
              <br />
              <br />
              <p>
                {t(
                  "Beta Lift Asia has given world a glimpse into future of vibrant cities."
                )}
              </p>
              <Link to="#" className="carousel-button  mr-2">
                {t("Register")}
              </Link>
              <Link to="#" className="carousel-button ">
                {t("Learn How")}
              </Link>
            </div>
          </Carousel.Item>

          <Carousel.Item>
            <img className="d-block w-100" src={fourthImg} alt="Third slide" />
            <div className="carousel-item-text-wrapper">
              <h2 class="carousel-title ">
                {t("Simplify your service selection")}
              </h2>
              <p>
                {t(
                  " When it comes to service needs, every building is different. Thats why Beta Lift Asia is here to help you find the right service plan with six simple questions."
                )}
              </p>
              <Link to="#" className="carousel-button">
                {t("GET STARTED")}
              </Link>
            </div>
          </Carousel.Item>
        </Carousel>
      </div>
    </div>
  );
}

export default CarouselComponent;
