import React from "react";
import { Container, Row, Col } from "reactstrap";
import AdditionalPart from "./AdditionalPart";
import resourseImage1 from "./otherLink1.jpg";
import resourseImage2 from "./otherLink2.jpg";
import "./serviceMaintenance.scss";
import headerBackground from "./headerBackground.webp";
import servicesLogos from "./serviceLogos.png";

// Import equipment items images
import equipmentItem1 from "./equipmentItem1.png";
import equipmentItem2 from "./equipmentItem2.png";
import equipmentItem3 from "./equipmentItem3.png";
import equipmentItem4 from "./equipmentItem4.png";

// Import About Us Image
import aboutUsImageBackground from "./sectionBackground.jpg";

// Import Equipment Background Image
import equipmentImageBackground from "./equipmentImageBackground.jpg";

// Import Experience Items Images
import experienceItem1 from "./individualIcon1.png";
import experienceItem2 from "./individualIcon2.png";

// Import Individual Maintenance Images
import individualImageItem1 from "./individualImage1.jpg";
import individualImageItem2 from "./individualImage2.jpg";
import individualImageItem3 from "./individualImage3.jpg";

// Import Item Images
import itemImage1 from "./item1.jpg";
import itemImage2 from "./item2.jpg";
import itemImage3 from "./item3.jpg";
import Flip from "react-reveal/Flip";
import Roll from "react-reveal/Roll";
import Fade from "react-reveal/Fade";
import Pulse from "react-reveal/Pulse";

// Import Translation Module
import { useTranslation } from "react-i18next";

// ServiceMaintenance Component
function ServiceMaintenance() {
  const { t } = useTranslation();

  return (
    <section id="service-maintenance" className="service-maintenance">
      <div className="header-wrapper">
        <header>
          <img
            src={headerBackground}
            alt="Products Background"
            className="header-background"
          />
          <Container className="header-inner">
            <Row className="d-flex justify-content-center">
              <Col lg="auto" className="d-flex flex-column align-items-center">
                <img
                  src={servicesLogos}
                  alt="Logos"
                  className="service-logos"
                />
                <Roll top>
                  <h1>
                    {t("Exceptional Service to the highest safety standards")}
                  </h1>
                </Roll>
                <Fade right>
                  <p>{t("Contact us to discuss you service requirement")}</p>
                </Fade>
                <Fade left>
                  <button>{t("watch our service video")}</button>
                </Fade>
              </Col>
            </Row>
          </Container>
        </header>
      </div>
      <main>
        <article className="needs">
          <Container>
            <Row className="d-flex justify-content-center">
              <Col lg="7" className="d-flex flex-column align-items-center">
                <Roll top>
                  <h2>
                    {t(
                      "Together, we find the maintenance contract tailored to your needs"
                    )}
                  </h2>
                </Roll>
                <Fade right>
                  <p>
                    {t(
                      "Help us make the best contract recommendation for you in a few steps with our maintenance configurator"
                    )}
                  </p>
                </Fade>
                <Fade left>
                  <button>{t("find my contract")}</button>
                </Fade>
              </Col>
            </Row>
          </Container>
        </article>
        <article className="equipment">
          <Container>
            <Row>
              <Col lg="6" className="equipment-items-container">
                <Fade left>
                  <h3>
                    {t("No one knows your equipment like Beta Lift Asia")}
                  </h3>
                  <p>
                    {t(
                      "Have your Beta Lift Asia and non-Beta Lift Asia units serviced by the largest elevator and escalator provider in the world."
                    )}
                  </p>
                  <div className="equipment-items-container-wrap">
                    <div className="equipment-item">
                      <img src={equipmentItem1} alt="Equipment Item" />
                      <h4>{t("+ Certified OEM parts")}</h4>
                    </div>
                    <div className="equipment-item">
                      <img src={equipmentItem2} alt="Equipment Item" />
                      <h4>{t("+ Mechanic expertise & specialized roles")}</h4>
                    </div>
                    <div className="equipment-item">
                      <img src={equipmentItem3} alt="Equipment Item" />
                      <h4>{t("+ Regular software updates")}</h4>
                    </div>
                    <div className="equipment-item">
                      <img src={equipmentItem4} alt="Equipment Item" />
                      <h4>{t("+ Connected technologies")}</h4>
                    </div>
                  </div>
                  <p>{t("= Customized maintenance program")}</p>
                </Fade>
              </Col>
              <Col lg="6" className="equipment-image-container">
                <img
                  src={equipmentImageBackground}
                  alt={t("Equipemnt Image")}
                />
              </Col>
            </Row>
          </Container>
        </article>
        <div className="about-us-wrapper">
          <article className="about-us">
            <img
              src={aboutUsImageBackground}
              alt={t("About Us Image")}
              className="about-us-background-image"
            />
            <Container>
              <Row className="d-flex justify-content-center">
                <Col lg="7" className="d-flex align-items-center flex-column">
                  <Roll top>
                    <p>{t("Dedicated Elevator Repair Experts")}</p>7
                  </Roll>
                  <Fade right>
                    <h2>
                      {t(
                        "Our committed teams are invested in the future of your elevators"
                      )}
                    </h2>
                  </Fade>
                  <Fade left>
                    <p>
                      {t(
                        "Every day, our teams roll up their sleeves to keep two billion people on the move. We continue to invest in new tools and processes, so our highly trained mechanics and support teams can ensure a faster return to service. Its all part of our commitment to moving the world more safely, efficiently and reliably."
                      )}
                    </p>
                  </Fade>
                </Col>
              </Row>
            </Container>
          </article>
        </div>
        <article className="experience">
          <Container>
            <div
              className="d-flex flex-column align-items-center"
              style={{ padding: "75px 0" }}
            >
              <Roll top>
                <h2 className="text-center">
                  {t("A more personalized experience")}
                </h2>
              </Roll>
              <Fade right>
                <span className="d-block text-center text-md-left">
                  {t(
                    "We put you and your passengers at the center of everything we do"
                  )}
                </span>
              </Fade>
            </div>
            <Fade left>
              <Row style={{ padding: "75px 0" }}>
                <Col
                  lg="6"
                  className="experience-item d-flex flex-column align-items-center"
                >
                  <img src={experienceItem1} alt={t("Experience Item One")} />
                  <h3>{t("Personalized contracts")}</h3>
                  <p>
                    {t(
                      "Flexible service packages offer maintenance and connectivity options to suit your needs."
                    )}
                  </p>
                </Col>
                <Col
                  lg="6"
                  className="experience-item d-flex flex-column align-items-center"
                >
                  <img src={experienceItem2} alt={t("Experience Item Two")} />
                  <h3>{t("Capital planner")}</h3>
                  <p>
                    {t(
                      "Tailored recommendations help you avoid surprises, increase product lifespans and plan for the future."
                    )}
                  </p>
                </Col>
              </Row>
            </Fade>
            <Fade right>
              <Row
                className="d-flex align-items-center flex-row justify-content-between"
                style={{ padding: "75px 0" }}
              >
                <Col lg="6" className="maintenance-info-item">
                  <h3>{t("Real time maintenance information")}</h3>
                  <p>
                    {t(
                      "Receive information on demand with our customer portal"
                    )}
                  </p>
                  <p>
                    {t(
                      "We are committed to being easy to do business with. That means giving you a single place to find all the information you need when you need it. With the customer portal, you can get a complete overview of your account, available 24/7. Contact our teams directly via phone or online request and track maintenance, performance and billing all in one place."
                    )}
                  </p>
                </Col>
                <Col lg="6" className="maintenance-info-item">
                  <img
                    src={individualImageItem1}
                    alt={t("Maintenance Info Image")}
                  />
                </Col>
              </Row>
            </Fade>

            <Row
              className="d-flex flex-row justify-content-between"
              style={{ padding: "25px 0" }}
            >
              <Col
                lg="4"
                md="6"
                sm="12"
                className="service-info-item d-flex flex-column align-items-center mb-5"
              >
                <Pulse>
                  <div className="service-info-item-image-container">
                    <img src={itemImage1} alt={t("Item 1")} />
                  </div>
                  <div className="service-info-item-data-container">
                    <h3>{t("eService")}</h3>
                    <h4>
                      {t(
                        "Manage your entire service portfolio at your desk or on the go"
                      )}
                    </h4>
                    <p>
                      {t(
                        "Our eService portal provides you instant access to critical elevator equipment and service information, on your computer or mobile device. View the performance dashboard, check maintenance details or place a service cal"
                      )}
                    </p>
                    <a href="#!">{t("download factsheet")}</a>
                  </div>
                </Pulse>
              </Col>
              <Col
                lg="4"
                md="6"
                sm="12"
                className="service-info-item d-flex flex-column align-items-center mb-5"
              >
                <Pulse>
                  <div className="service-info-item-image-container">
                    <img src={itemImage2} alt={t("Item 2")} />
                  </div>
                  <div className="service-info-item-data-container">
                    <h3>{t("eService mobile")}</h3>
                    <h4>
                      {t("Manage your equipment right from your smartphone")}
                    </h4>
                    <p>
                      {t(
                        "Stay up to date on your elevators service history and performance data with eService mobile, the industrys first app to deliver real-time account data right to your mobile device"
                      )}
                    </p>
                    <a href="#!">{t("download factsheet")}</a>
                  </div>
                </Pulse>
              </Col>
              <Col
                lg="4"
                md="6"
                sm="12"
                className="service-info-item d-flex flex-column align-items-center mb-5"
              >
                <Pulse>
                  <div className="service-info-item-image-container">
                    <img src={itemImage3} alt={t("Item 3")} />
                  </div>
                  <div className="service-info-item-data-container">
                    <h3>{t("Service eCatalog")}</h3>
                    <h4>{t("Find products to upgrade your equipment")}</h4>
                    <p>
                      {t(
                        "Our eCatalog helps you quickly find the products and services you need to upgrade your elevator and escalator and stay ahead of future needs."
                      )}
                    </p>
                    <a href="#!">{t("take me there")}</a>
                  </div>
                </Pulse>
              </Col>
            </Row>
          </Container>
        </article>

        <article className="additional-information">
          <Container>
            <Row className="d-flex flex-row mb-lg-0 mb-5">
              <Col
                lg="6"
                className="additional-info-data-container d-flex align-items-lg-start text-center text-lg-start align-items-center flex-column mb-lg-0 mb-5"
              >
                <Fade left>
                  <h2>
                    Beta Lift Asia LINE <sup></sup>
                  </h2>
                  <h3>{t("24/7 expert support")}</h3>
                  <p>
                    {t(
                      "Every hour of every day, the OTISLINE call center is here for you. This service puts you in touch with experts trained in every aspect of your systems operation and what to do when issues arise. With every call, the OTISLINE call center is the fast track to the resolution you need."
                    )}
                  </p>
                  <a href="#!">{t("Contact us")}</a>
                </Fade>
              </Col>
              <Col
                lg="6"
                className="additional-info-image-container d-flex flex-column"
              >
                <img
                  src={individualImageItem2}
                  alt={t("Individual Image Item")}
                />
              </Col>
            </Row>
            <Row className="d-flex flex-row-reverse mb-lg-0 mb-5">
              <Col
                lg="6"
                className="additional-info-data-container d-flex text-center text-lg-start align-items-lg-start align-items-center flex-column mb-lg-0 mb-5"
              >
                <Fade right>
                  <h2>{t("Upgrades & replacements")}</h2>
                  <h3>{t("Improving your investment")}</h3>
                  <p>
                    {t(
                      "Products are becoming faster, more reliable, safer and more environmentally friendly than ever before. And as older parts grow outdated, they can affect the look and feel of your building. Explore the many upgrades and replacement products we offer to protect your investment."
                    )}
                  </p>
                  <a href="#!">{t("EXPLORE UPGRADES & REPLACEMENTS")}</a>
                </Fade>
              </Col>
              <Col
                lg="6"
                className="additional-info-image-container d-flex align-items-start flex-column"
              >
                <img
                  src={individualImageItem3}
                  alt={t("Individual Image Item")}
                />
              </Col>
            </Row>
          </Container>
        </article>
        <article className="additional-part">
          <Container>
            <div className="additional-part-info d-flex text-md-start text-center flex-column align-items-center">
              <Roll top>
                <h1>{t("Discover a smarter vision of service")}</h1>
              </Roll>
              <Fade right>
                <p>
                  {t(
                    "Otis Signature Service combines modern intelligence with a legacy of commitment to deliver you with a more personalized service experience."
                  )}
                </p>
              </Fade>
              <Fade left>
                <a href="#!">{t("download brochure")}</a>
              </Fade>
            </div>
            <Row className="additional-part-links d-flex flex-wrap justify-content-between align-items-start flex-row">
              <AdditionalPart image={resourseImage1} title="resource library" />
              <AdditionalPart image={resourseImage2} title="contact us" />
            </Row>
          </Container>
        </article>
      </main>
    </section>
  );
}

export default ServiceMaintenance;
