import React from "react";
import Carousel from "react-bootstrap/Carousel";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import img1 from "./images/secondCarusel-slide-1.jpg";
import img2 from "./images/secondCarusel-slide-2.jpg";
import img3 from "./images/secondCarusel-slide-3.jpg";
import img4 from "./images/secondCarusel-slide-4.jpg";

function SecondCarousel() {
  const { t } = useTranslation();
  return (
    <div className="carousel-wrapper">
      <div className="Carousel">
        <Carousel fade>
          <Carousel.Item>
            <img className="d-block w-100" src={img1} alt="First slide" />
            <div className="carousel-item-text-wrapper">
              <h2 className="carousel-title ">{t("The new Gen3 elevator")}</h2>
              <p>{t("Meet our fully connected platform.")}</p>
              <Link to="#" className="carousel-button-dark">
                {t(" Learn More")}
              </Link>
            </div>
          </Carousel.Item>

          <Carousel.Item>
            <img className="d-block w-100" src={img2} alt="Second slide" />
            <div className="carousel-item-text-wrapper">
              <h2>
                {t('"Connected Elevators')} <br />
                {t(" is our collective future.")} <br />
                {t('Welcome to Tomorrow"')}
              </h2>
              <p>{t("-Judy Marks, Otis president & CEO")}</p>
              <br />
              <br />
              <p>
                {t(
                  "Otis has given world a glimpse into future of vibrant cities."
                )}
              </p>
              <Link to="#" className="carousel-button-dark mr-2">
                {t("Register")}
              </Link>
              <Link to="#" className="carousel-button-dark ">
                {t("Learn How")}
              </Link>
            </div>
          </Carousel.Item>

          <Carousel.Item>
            <img className="d-block w-100" src={img3} alt="Third slide" />
            <div className="carousel-item-text-wrapper">
              <h2 class="carousel-title ">
                {t("Made to Move Communities")}
                <sup>TM</sup>
              </h2>
              <p>
                {t(
                  "Students around the globe spent eight weeks working with Otis mentors and using STEM skills to solve mobility challenges in their communities."
                )}
              </p>
              <Link to="#" className="carousel-button-dark">
                {t("EXPLORE MADE TO MOVE COMMUNITIES")}
              </Link>
            </div>
          </Carousel.Item>

          <Carousel.Item>
            <img className="d-block w-100" src={img4} alt="Third slide" />
            <div className="carousel-item-text-wrapper">
              <h2 class="carousel-title ">{t("Upgrades made easy")}</h2>
              <p>
                {t(
                  "With our eCatalog, Beta Lift Asia can help you quickly find the products and services you need to upgrade your elevator and escalator systems and stay ahead of future needs."
                )}
              </p>
              <Link to="#" className="carousel-button-dark">
                {t("EXPLORE UPGRADE OPTIONS")}
              </Link>
            </div>
          </Carousel.Item>
        </Carousel>
      </div>
    </div>
  );
}

export default SecondCarousel;
